import { FocusTrap } from "focus-trap-vue";
import RoaIconButton from "./RoaIconButton.vue";
export default {
  components: { FocusTrap, RoaIconButton },
  props: {
    label: { type: String },
  },
  data() {
    return { isOpen: false };
  },
  methods: {
    toggle() {
      this.isOpen = !this.isOpen;
    },
  },
};
