export default {
  props: {
    maxValue: {
      type: Number,
      required: true,
    },
    currentValue: {
      type: Number,
      required: true,
    },
  },
  computed: {
    style() {
      return {
        transform: `scaleX(${
          this.currentValue / this.maxValue
        }) translateY(1px)`,
      };
    },
  },
};
