import SliderWrapper from "../components/SliderWrapper";
export default {
  components: {
    SliderWrapper,
  },
  props: { slides: { type: Array, required: true } },
  data() {
    return {
      glide: null,
    };
  },
  computed: {
    currentSlide() {
      const index = this.glide?.index || 0;
      return this.slides[index];
    },
  },
  methods: {
    showPrevious() {
      this.glide?.go("<");
    },
    showNext() {
      this.glide?.go(">");
    },
  },
};
