export default {
  props: {
    artwork: { type: Object, required: true },
    dragConfidence: { type: Number, required: true },
    likes: { type: Boolean, required: false },
  },
  computed: {
    opacity() {
      const threshold = 0.1;
      const value = (this.dragConfidence - threshold) * 3;
      return this.dragConfidence < threshold ? 0 : value;
    },
  },
};
