import jsonp from "jsonp";
import queryString from "query-string";

export default {
  props: {
    userId: {
      required: true,
      type: String,
    },
    listId: {
      required: true,
      type: String,
    },
    cta: { type: String, default: "Join" },
    white: { type: Boolean, default: false },
  },

  data() {
    return {
      email: "",
      success: false,
      error: null,
      loading: false,
      uid: null,
      url: "https://returnonart.us16.list-manage.com/subscribe/post-json",
    };
  },

  computed: {
    user() {
      return this.$auth.currentUser;
    },
    data() {
      return queryString.stringify({
        u: this.userId,
        id: this.listId,
        EMAIL: this.email,
        ARTMATCHID: this.uid,
      });
    },
  },
  watch: {
    user(user) {
      if (user?.email) this.email = user.email;
    },
  },
  methods: {
    subscribe() {
      if (this.email === null || this.loading) {
        return;
      }

      this.success = false;
      this.error = null;
      this.loading = true;
      this.uid = localStorage.getItem("uid");

      const url = `${this.url}?${this.data}`;
      jsonp(url, { param: "c" }, this.onResponse);
    },

    onResponse(error, data) {
      this.loading = false;

      if (error) {
        this.error = error;
      }

      if (data && data.result === "error") {
        this.error = data.msg;
      }

      if (this.error) {
        this.$emit("error", this.error);
      } else {
        this.success = true;
        this.email = "";
        this.$emit("success");
      }
    },
  },
};
