export default {
  props: {
    label: { type: String, required: true },
    name: { type: String },
    autocomplete: { type: String },
    error: { type: String },
    required: { type: Boolean, default: false },
    disabled: { type: Boolean, default: false },
    novalidate: { type: Boolean, default: false },
    placeholder: { type: String },
    value: { type: String | null, required: true },
  },
  data() {
    return {
      focused: false,
      nativeErrorMessage: "",
      mounted: false,
      open: false,
    };
  },
  mounted() {
    this.mounted = true;
  },
  computed: {
    options() {
      if (!this.mounted) return [];
      const options = [...this.$refs.select.options]
        .filter((option) => option && option.value !== null)
        .map((option) => {
          return {
            text: option.label,
            value: option.value,
          };
        });
      return options;
    },
    selectedOption() {
      if (!this.mounted) return {};
      if (this.value === null) return {};
      const selectedOption = this.options.find(
        (option) => option.value === this.value
      );
      if (!selectedOption) this.$emit("input", null);
      return selectedOption;
    },
  },
  methods: {
    handleBlur(event) {
      this.focused = false;
      this.nativeErrorMessage = event.target.validationMessage;
    },
    selectOption(option) {
      this.$emit("input", option.value || "");
      this.open = false;
    },
  },
};
