import Vue from "vue";

function slugify(text) {
  return text
    .normalize("NFD")
    .replace(/[\u0300-\u036f]/g, "")
    .replaceAll(" ", "-")
    .toLowerCase();
}
window.slugify = slugify;
export default {
  props: ["data"],
  data() {
    return {
      showSubscribeModal: false,
      subscribed: false,
      view: "column",
      macy: null,
    };
  },
  computed: {
    extendedArtworks() {
      return this.data.artworks.map((artwork) => ({
        ...artwork,
        artist:
          typeof artwork.artist === "string"
            ? {
                title: artwork.artist,
                url: `/artists/${slugify(artwork.artist)}`,
              }
            : artwork.artist,
      }));
    },
  },
  updated() {
    Vue.nextTick(() => {
      this.macy?.recalculate(true, true);
    });
  },
  methods: {
    toggleSubscribeModal() {
      this.showSubscribeModal = !this.showSubscribeModal;
    },
    onSuccess() {
      setTimeout(() => {
        this.showSubscribeModal = false;
        this.subscribed = true;
      }, 5000);
    },
    formatPrice(price) {
      return parseFloat(price).toLocaleString("en") + ".00";
    },
  },
};
