import { mapGetters } from "vuex";
import { FocusTrap } from "focus-trap-vue";
export default {
  components: { FocusTrap },
  computed: {
    ...mapGetters(["cart", "currentCurrency", "total", "cartLoading"]),
    cartItems() {
      return this.cart.items || [];
    },
  },
};
