import { mapGetters } from "vuex";
export default {
  computed: {
    ...mapGetters(["currencies", "currentCurrency"]),
  },
  methods: {
    updateCurrency(newCode) {
      this.$store.dispatch("setCurrency", newCode);
    },
  },
};
